.post {
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: white;
  margin-top: 10px;
  border: 1px solid lightgray;
  border-radius: 5px;
  box-shadow: 0px 5px 8px -9px solid rgab(0, 0, 0, 0.5);
}

.post_info {

  align-items: center;
}

.post_info > h4 {
  margin-left: 10px;
  cursor: pointer;
  font-size: 13px;
}
.post_info > h4:hover {
  text-decoration: underline;
}

.post_info > small {
  margin-left: 10px;
}

.post_body {
  display: flex;
  flex-direction: column;
}

.post_body > .post_question {
  margin-top: 10px;
  font-weight: bold;
  margin-bottom: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  flex: 1;
}

.post_question > .post_button {
  margin-left: auto;
  cursor: pointer;
  padding: 7px;
  background-color: blue;
  outline: none;
  border: none;
  color: lightgray;
  font-weight: 300;
  font-size: 14px;
  border-radius: 33px;
}

.post_button:hover {
  color: blue;
  background: lightgray;
}

.post_question > p:hover {
  text-decoration: underline;
}

.post_body > .post_answer > p {
  margin-bottom: 10px;
}

/* .post_image {
  background: rgb(0, 140, 255);
} */

.post_body > img {
  width: 100%;
  object-fit: contain;
  background-color: rgb(238, 238, 238);
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.post_footer {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.post_footer > .MuiSvgIcon-root {
  color: gray;
  margin-right: 40px;
  cursor: pointer;
}

.post_footerAction {
  background-color: lightgray;
  padding: 5px;
  align-items: center;
  display: flex;
  justify-content: space-around;
  border-radius: 33px;
}
.post_footerAction > .MuiSvgIcon-root {
  color: gray;
  margin-right: 40px;
  cursor: pointer;
}

.post_footerAction > .MuiSvgIcon-root:hover {
  color: rgb(0, 140, 255);
  margin-right: 40px;
}

.post_footerLeft {
  margin-left: auto;
}

.post_footerLeft > .MuiSvgIcon-root {
  color: gray;
  cursor: pointer;
  margin-left: 30px;
}
  .modal_question {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .modal_question > h1 {
    color: #8f1f1b;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .modal_question > p {
    color: gray;
    font-size: small;
  }
  
  .modal_question > p > .name {
    color: black;
    font-weight: bold;
  }
  
  .modal_answer {
    display: flex;
    padding-top: 20px;
    flex: 1;
  }
  
  .modal_answer > textarea {
    width: 100%;
    height: 200px;
    padding: 5px;
    font-size: 15px;
    color: blue;
  }
  
  
.modal__button {
  display: flex;
  align-items: center;
  margin-right: -20px;
  border-top: 1px solid lightgray;
}

.modal__button > .cancle {
  margin-left: auto;
  border: none;
  margin-top: 5px;
  outline: none;
  color: gray;
  font-weight: 500;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
}
.modal__button > .add {
  border: none;
  outline: none;
  margin-top: 5px;
  background-color: rgb(0, 64, 255);
  color: white;
  font-weight: 700;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
}